
console.log("Hello World!");

window.$ = window.jQuery = require('jquery');
import * as bootstrap from 'bootstrap';
window.bootstrap = bootstrap;


const toastTrigger = document.getElementById('liveToastBtn')
const toastLiveExample = document.getElementById('liveToast')

$(document).ready(function () {
    const popoverTriggerList = document.querySelectorAll('[data-bs-toggle="popover"]');
    const popoverList = [...popoverTriggerList].map(popoverTriggerEl => new bootstrap.Popover(popoverTriggerEl));

    $('.button-modal').on('click', function (){
        console.log("Succsess")
        let modal_title = $(document).getElementById("exampleModalLabel")
        let modal_description = $(document).getElementById("modal_description")
        $.ajax({
            url: "/show",
            method: 'get',
            dataType: 'html',
            data: {"elem": $(this).id},

            success: function(data){
                $(modal_title).value = data['modal_title'];
                $(modal_description).value = data['modal_description'];
            }
        })
    })

});

if (toastTrigger) {
    const toastBootstrap = bootstrap.Toast.getOrCreateInstance(toastLiveExample)
    toastTrigger.addEventListener('click', () => {
        toastBootstrap.show()
    })
}


$(document).on('keydown', '.modal', function(e) {
    if(e.key==='ArrowRight'){
        document.getElementById('r-btn' + this.getAttribute('num')).click();
    }
    if(e.key==='ArrowLeft'){
        document.getElementById('l-btn' + this.getAttribute('num')).click();
    }
});
